import Vue from 'vue'
import Vuex from 'vuex'
import state from './state'

//  vue.use vuex
Vue.use(Vuex)

// vuex
export default new Vuex.Store({
  state,
  actions: {
    // ctx,commit
    changeCity (ctx, city) {
      // changecitymutations
      ctx.commit('changeCity', city)
    }
  },
  // getters: {
  //   doubleCity (state) {
  //     return state.city + '' + state.city
  //   }
  // },
  mutations: {
    changeCity (state, city) {
      state.city = city
      // 
      // localStorage.city = city
      try {
        localStorage.city = city
      } catch (e) {}
    }
  }
})



